import React, {useState, useEffect} from 'react';
import CollectData from "./CollectData"
import { MyCarousel } from './MyCarousel';  
import { CarouselDualis } from './CarouselDualis';  
import { MyParallax } from './MyParallax';
import { FullscreenBanner } from './FullscreenBanner';
import { InfoPanel } from './InfoPanel';
import { SphereView } from './SphereView';
import { Helper } from '../Helper';
import Preload from 'react-preload'
import csvToJSON from "./csvToJSON";
import {Innovacio} from "./Innovacio"


export const Home=()=>{

    const [csvData,setcsvData]=useState([])
    const[loaded,setLoaded]=useState(false)
    const [dualCsvData,setDualCsvData]=useState([])
    const[dualLoaded,setDualLoaded]=useState(false)
    const [innovacioCsvData,setInnovacioCsvData]=useState([])
    const[innovacioLoaded,setInnovacioLoaded]=useState(false)

    useEffect(()=>{
        // MyCarousel kp betlts
        if(!loaded){
            const url="https://raw.githubusercontent.com/kmagdi/KSZC-Data/master/reszletekKK.csv"
            fetch(url)           
                .then(resp=>resp.text())
                .then(text=>{
                    const adatokJSON=csvToJSON(text, ';')
                    //console.log(adatokJSON)
                    const filtered=adatokJSON.filter(obj=>obj.kod!==undefined&&obj.kod!=='0'&&obj.kod!=="")
                    setcsvData(filtered)
                },[])
            setLoaded(true)
        }
        // DualisCarousel kp betlts
        if(!dualLoaded){
            const url="https://raw.githubusercontent.com/kmagdi/KSZC-Data/master/dualis.csv"
            fetch(url)           
                .then(resp=>resp.text())
                .then(text=>{
                    const adatokJSON=csvToJSON(text, ';')
                    const filtered=adatokJSON.filter(obj=>obj.kod!==undefined&&obj.kod!=='0'&&obj.kod!=="")
                    setDualCsvData(filtered)
                },[])
            setDualLoaded(true)
        }

        // innovacios kp betlts
      if(!innovacioLoaded){
        const url="https://raw.githubusercontent.com/kmagdi/KSZC-Data/master/innovacio.csv"
        fetch(url)           
            .then(resp=>resp.text())
            .then(text=>{
                const adatokJSON=csvToJSON(text, ';')
                const filtered=adatokJSON.filter(obj=>obj.kod!==undefined&&obj.kod!=='0'&&obj.kod!=="")
                setInnovacioCsvData(filtered)
            },[])
        setInnovacioLoaded(true)
    }

    })
      

    const images = {
        head: Helper.getMODI(require('./assets/head.jpg'),require('./assets/head_mobile.jpg')),
        other: Helper.getMODI(require('./assets/eszt2.jpg'),require('./assets/eszt2_mobile.jpg')),
        mozaik1: Helper.getMODI(require('./assets/mozaik1.jpg'),require('./assets/mozaik1_mobile.jpg')),
        mozdony: Helper.getMODI(require('./assets/mozdony.jpg'),require('./assets/mozdony_mobile.jpg')),
        logo: require('./assets/logo.png')
    };
    //console.log(Object.values(images).concat(csvData.map((i)=>('./assets/' + i.kod + '.jpg'))));
    let carouselImages = csvData.map((i)=>([Helper.getMODI(require('./assets/' + i.kod + '.jpg'),require('./assets/' + i.kod + '_mobile.jpg')),i]));
    let dualImages = dualCsvData.map((i)=>([Helper.getMODI(require('./assets/dualis/' + i.kod + '/head.jpg'),require('./assets/dualis/' + i.kod + '/head_mobile.jpg')),i]));
    let innovacioImages=innovacioCsvData.map((i)=>([Helper.getMODI(require('./assets/innovacio/' + i.kod + '/head.jpg'),require('./assets/innovacio/' + i.kod + '/head_mobile.jpg')),i]));
    return(
        <Preload
            loadingIndicator={(
                <div style={{width:'100vw',height:'100vh',backgroundColor:'#343a40',color:'white'}}>
                    <div className="kozep">
                        {/* <h1>Kpek betltse</h1> */}
                        <img src={require('./assets/loading.gif')} alt={"logo"} />
                    </div>
                </div>
            )}
            images={dualImages.map((i)=>(i[0])).concat(carouselImages.map((i)=>(i[0]))).concat(Object.values(images))}
            autoResolveDelay={null}
        >
            <>
                <FullscreenBanner id="home" bgimage={images.head} image={images.logo} title={'A jvd itt kezddik'} />

                <InfoPanel id="iskola" size={6} type="centerInfo" title="Kezdetek" text="A Kecskemti SZC Kand Klmn Technikum a vros s trsge meghatroz szakkpzst folytat intzmnye, a 2018/2019-es tanvben nnepelte fennllsnak 50. vforduljt. A maximlis tanuli ltszm az 1990/91-es tanvben volt, 1502-en koptattk az iskolai padokat. Jelents eredmnynek tartjuk, hogy az iskola folyamatosan tartani tudja az 1100 f krli tanuli ltszmot a demogrfiai vlgy ellenre is." />

                <MyParallax img={images.mozaik1} amount={0.25} height={'40vh'} sizeToContent={true}>
                    <InfoPanel id="par1" bold={true} quote={true} author="Szentgyrgyi Albert" type="centerInfo" bgColor={"rgba(255,255,255,0.5)"} color="white" nomargin={true} height={'40vh'} text='Az iskola arra val, hogy az ember megtanuljon tanulni, hogy felbredjen tudsvgya, megismerje a jl vgzett munka rmt, megzlelje az alkots izgalmt, s megtallja a munkt, amit szeretni fog.' from={{opacity:0,scaleX:0.5,scaleY:0.5,ease:'power4.out'}} to={{opacity:1,scaleX:1.0,scaleY:1.0,ease:'power4.out'}} />
                </MyParallax>

                <InfoPanel id="jelen" className="sidebyside-margins" type="sideBySide" title="A jelen" text={<>Az oktatott szakkpestsek / szakmk kre folyamatosan vltozik, a mindenkori munkaer-piaci ignyeknek megfelelen. A jelenlegi kpzsi szerkezethez nagyvllalati kpzhelyek - dulis partnerek - kapcsoldnak, akikkel a beiskolzsi ignyek egyeztetstl a szakmai vizsgk megszervezsig szorosan egyttmkdnk. A kereslet-vezrelt szakkpzs elvrsainak a jelenlegi nagyvllalati partnereink segtsgvel mr most is meg tudunk felelni, clunk, hogy jabb vllalkozsokat is partnereink kztt tudhassunk.<br/><b>Kattints a kpre a nagy mretben val megtekintshez!</b></>} image={images.head} clickable={true} from={{opacity:0,x:200,ease:'power4.out'}} to={{opacity:1,x:0,ease:'power4.out',stagger:{amount:0.2}}} />

                <MyParallax img={images.mozdony} amount={0.25} height={'40vh'} sizeToContent={true}>
                    <InfoPanel id="par2" bold={true} quote={true} author="Marie Curie" type="centerInfo" bgColor={"rgba(255,255,255,0.75)"} color="white" nomargin={true} height={'40vh'} text='Egyiknk lete sem knny. s akkor? Legyen bennnk kitarts, s mindenekeltt bzzunk nmagunkban. Hinnnk kell benne, hogy tehetsgesek vagyunk valamiben, s ezt a valamit - kerl, amibe kerl - meg tudjuk valstani.' from={{opacity:0,scaleX:0.5,scaleY:0.5,ease:'power4.out'}} to={{opacity:1,scaleX:1.0,scaleY:1.0,ease:'power4.out'}} />
                </MyParallax>

                <InfoPanel id="jovo" className="sidebyside-margins" type="sideBySide" title="A jv" text="A technikai- technolgiai fejlds, a digitalizci, a az ipar 4.0 technikai kihvsai a szakmai kpzs szmra is folyamatos kihvst jelentenek, melynek igyeksznk folyamatosan megfelelni. Ehhez jelenleg is folyik a szakkpzs tartalmi s mdszertani talaktsa, valamint az infrastrukturlis felttelek folyamatos fejlesztse. A Kand tfog korszersts s bvts eltt ll, melynek elkszt munklatai mr elkezddtek. A Kand mindig klns gondot fordtott a szakmai oktats mellett a kultra kzvettsre, a tanulk egyni kpessgeinek fejlesztsre, a tehetsggondozsra. Tanulink kiemelked orszgos versenyeredmnyeire klnsen bszkk vagyunk." reverse={true} image={images.head} from={{opacity:0,x:-200,ease:'power4.out',stagger:{amount:0.2}}} to={{opacity:1,x:0,ease:'power4.out'}} />

                <div id="szakok" className="row justify-content-center">
                    <MyCarousel kepek={carouselImages} />
                </div>

                <InfoPanel id="buszke" className="sidebyside-margins" type="sideBySide" title="Amikre bszkk vagyunk" text="Ez nem felttlen gy lesz, csak egyelre gy lett berakva, hogy legalbb azt lssuk, hogy hol fog elhelyezkedni ez a szekci." image={images.head} from={{opacity:0,x:200,ease:'power4.out',stagger:{amount:0.2}}} to={{opacity:1,x:0,ease:'power4.out'}} />

                <div id="dualis" className="row justify-content-center">
                    <CarouselDualis kepek={dualImages} />
                </div>
                

                {/* <MyParallax img={images.head} amount={0.25} height={'40vh'} sizeToContent={true}>
                    <InfoPanel id="else1" bold={true} type="centerInfo" bgColor={"rgba(255,255,255,0.5)"} nomargin={true} height={'40vh'} title="Something" text="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus risus diam, euismod non orci ut, ornare varius eros. Cras pellentesque, sapien in consequat accumsan, nunc quam pulvinar nulla, sodales rhoncus diam mi vitae tellus. Nunc vulputate ligula nisl, id dignissim turpis vulputate sed. Aliquam tincidunt porttitor odio sed vulputate. Aliquam sit amet maximus mi, a fringilla urna. Ut quis sem a mauris facilisis rhoncus." from={{opacity:0,scaleX:0.5,scaleY:0.5,ease:'power4.out'}} to={{opacity:1,scaleX:1.0,scaleY:1.0,ease:'power4.out'}} />
                </MyParallax> */}

                <InfoPanel id="else2" className="sidebyside-margins" type="sideBySide" title="Amikre bszkk vagyunk" text="Ez nem felttlen gy lesz, csak egyelre gy lett berakva, hogy legalbb azt lssuk, hogy hol fog elhelyezkedni ez a szekci." reverse={true} image={images.head} from={{opacity:0,x:-200,ease:'power4.out',stagger:{amount:0.2}}} to={{opacity:1,x:0,ease:'power4.out'}} />

                <div id="innovacio" style={{margin:'0 0 1rem 0'}}>
                    <h2 style={{textAlign:'center'}}>Innovcik:</h2>
                    <Innovacio kepek={innovacioImages} />
                </div>

                <div className={'panorama-container-container'}>
                    <h2>Ezeknek is kelleni fog majd egy kln szekci:</h2>
                    <SphereView id="1" image={Helper.getMODI(require('./assets/pano1.jpg'),require('./assets/pano1.jpg'))} />
                    <SphereView id="2" image={Helper.getMODI(require('./assets/pano2.jpg'),require('./assets/pano2.jpg'))} />
                </div>

                <CollectData bgimage={images.head} />
            </>
        </Preload>

    )
}